<template>
  <div v-show="isVisible" class="box_keypad_wrap active" @click="hide">
    <div class="inner_keypad_wrap" @click.stop="">
      <h2 class="text_title message1"> CVC 번호를 입력해주세요.</h2>
      <div calss="box_keyinput">
        {{ text }}
      </div>
      <div class="box_keypad" :style="{ height: height }">
        <nos-keypad
          ref="extendui-keypad"
          :uuid="uuid"
          :input="input"
          :keyset="keyset"
          :absolute-use="absoluteUse"
          @hide="onHide"
          @resize="onResize"
          @update="onUpdate"
        ></nos-keypad>
      </div>
    </div>
  </div>
</template>

<script>
import NOSKeypad from '../NOSKeypad';

export default {
  components: {
    'nos-keypad': NOSKeypad,
  },
  props: {
    input: {
      required: true,
      type: HTMLInputElement,
    },
    keyset: {
      type: Object,
      required: true,
    },
    uuid: {
      type: String,
      required: true,
    },
    absoluteUse: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isVisible: false,
      keypad: null,
      height: 0,
      text: '',
    };
  },
  computed: {
    // style : function() {
    //   return this.keypad ? {height : this.keypad.height() } : {};
    // }
  },
  created() {
    //
  },
  mounted() {
    this.keypad = this.$refs['extendui-keypad'];
    window.addEventListener('resize', this.handleResize);
    /// /window.addEventListener('scroll', this.show);
  },
  beforeDestroy() {
    // console.log("beforeDestroy...");
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    handleResize(event) {
      // this.calculateRatio();
      // this.calculatePosition();
    },
    onHide(event) {
      this.isVisible = false;
      this.$emit('hide');
    },
    onResize() {
      this.height = this.keypad.height() + 'px';
    },
    onUpdate() {
      this.text = this.keypad ? this.keypad.value.formatted : '';
      if (this.keypad) {
        this.$emit('update', this.keypad.val());
      }
    },
    show() {
      this.height = this.keypad.height() + 'px';
      this.keypad.show();
      this.isVisible = true;
    },
    hide() {
      this.keypad.hide();
      this.isVisible = false;
    },
    clear() {
      this.keypad.clear();
      this.isVisible = false;
    },
    isUseYn() {
      return this.keypad.isUseYn();
    },
    serialize() {
      return this.keypad.serialize();
    },
    val() {
      return this.keypad ? this.keypad.val() : {};
    },
  },
};
</script>
<style scoped>
body {
  margin: 0;
}
/* .lysckeypadbox{position:fixed; top:inherit; left:0; right:0; bottom:0; height:auto;} */
.box_keypad_wrap {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 0%;
  height: 0%;
  transition: width height 0s;
}
.box_keypad_wrap.active {
  width: 100%;
  height: 100%;
  transition: width height 0;
}
.box_keypad_wrap:after {
  content: '';
  display: block;
  z-index: 1003;
  position: fixed;
  left: 0;
  top: -100%;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  transition: opacity 0.8s; /* transition:top 0.9s; */
}
.box_keypad_wrap.active:after {
  top: 0;
  transition: top 0s;
  transition: opacity 0.5s;
  opacity: 1;
}
.box_keypad_wrap .inner_keypad_wrap {
  position: fixed;
  z-index: 1004;
  left: 0;
  bottom: -100%;
  width: 100%;
  height: auto;
  padding: 30px 20px 20px 20px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 15px 15px 0 0;
  transition: bottom 0.7s;
}
.box_keypad_wrap.active .inner_keypad_wrap {
  bottom: 0;
  transition: bottom 0.5s;
}
.text_title {
  padding: 0;
  margin: 0;
  color: #111;
  font-size: 17px;
  font-weight: 600;
  line-height: 1.2;
  text-align: center;
  background: #fff;
  z-index: 9;
}
.box_keyinput {
  display: inline-block;
  width: 100%;
  height: auto;
  margin-top: 30px;
  text-align: center;
  letter-spacing: -5px;
}
.box_keyinput > input {
  position: absolute;
  left: 0;
  top: 0;
  visibility: hidden;
}
.box_keyinput .keybox {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  text-align: center;
}
.box_keyinput .keybox ~ .keybox {
  margin-left: 15px;
}
.box_keyinput .keybox:after {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: #cccccc;
  border-radius: 20px;
}
.box_keyinput .keybox.on:after {
  background-color: #145e6f;
}
.box_keyinput .dashbox {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  text-align: center;
}
.box_keypad {
  position: relative;
  height: auto;
  margin-top: 50px;
  letter-spacing: -6px;
  text-align: center;
  display: flex;
  justify-content: center;
}
.button_sckeypad {
  width: 33.3%;
  height: 54px;
  padding: 0;
  margin: 0;
  border: 0;
  background-color: #fff;
  letter-spacing: 0;
  vertical-align: top;
  font-size: 25px;
  line-height: 54px;
  color: #000;
  font-weight: 700;
}
.button_sckeypad:active {
  color: #145e6f;
}
.button_sckeypad.key_reset,
.button_sckeypad.key_delete {
  position: absolute;
  bottom: 0;
}
.button_sckeypad.key_reset {
  left: 0;
  font-size: 19px;
}
.button_sckeypad.key_delete {
  right: 0;
  font-size: 28px;
}
</style>
<style scoped>
div.nppfs-keypad-wrap {
  position: relative;
  white-space: normal;
}
div.nppfs-keypad .kpd-group {
  position: relative;
  z-index: 10;
  width: 0px;
  height: 0px;
  white-space: normal;
}
div.nppfs-keypad .kpd-touch {
  position: relative;
  z-index: 30;
  white-space: normal;
}
div.nppfs-keypad .kpd-text {
  z-index: 40;
}
div.nppfs-keypad .kpd-preview {
  position: relative;
  z-index: 50;
  margin-left: 50%;
  white-space: normal;
}
div.nppfs-keypad .kpd-data {
  cursor: pointer;
}
.nppfs-keypad {
  top: 0px !important;
}
</style>
